import React from 'react';
import styled from 'styled-components';
import { COLORS } from '../../constants/colors';
import { adaptiveSize } from '../../functions/adaptiveSize';




const Button = styled.button`
    padding: 5px;
    border: none;
    border: 2px solid ${COLORS.starCommandBlue};
    border-radius: 0px 6px 0px 6px;
    background: ${props => props.background ? props.background : 'transparent'};
    cursor: pointer;
    
    color: ${props => props.color ? props.color : `${COLORS.starCommandBlue}`};;
    font-size: ${props => props.bigText ? adaptiveSize(1920, 24, 240, 18) : adaptiveSize(1920, 16, 240, 14)};
    padding-left: ${props => props.big ? '30px' : '5px'};
    padding-right: ${props => props.big ? '30px' : '5px'};
    transition: 0.3s;
    -webkit-transition: 0.3s;
    letter-spacing: 1.1px;
    max-width: 400px;
    &:hover{
        border-radius: 4px 15px 4px 15px;
        scale: ${props => props.scale ? '1.2' : '1'};
    }
`

export default Button;
