import React from 'react';
import styled from 'styled-components';

const Checkbox = ({ value, onClick }) => {
    return (
        <StyledCheckbox type='checkbox' value={value} onClick={onClick} />
    );
}

export default Checkbox;

const StyledCheckbox = styled.input`
    width: 30px;
    height: 30px;
    margin: 10px;
`