import React, { useState } from 'react';
import { ViewTitle } from '../atoms';
import Container from '../atoms/Container';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';

const codesPreview = [
    {
        title: 'КН-01 (SYNOP)',
        description: 'Далеко-далеко, за словесными горами в стране гласных и согласных живут рыбные тексты. Переписывается одна за заголовок города!',
        image: require('../../assets/images/codes/kn-01.jpg'),
        alt: 'Синоптический код КН-01',
        link: '/codes/KN-01',
    },
    {
        title: 'METAR и SPECI',
        description: 'Далеко-далеко, за словесными горами в стране гласных и согласных живут рыбные тексты. Переписывается одна за заголовок города!',
        image: require('../../assets/images/codes/metar.jpg'),
        alt: 'Код фактической погоды METAR',
        link: 'metar',
    },
    {
        title: 'TAF',
        description: 'Далеко-далеко, за словесными горами в стране гласных и согласных живут рыбные тексты. Переписывается одна за заголовок города!',
        image: require('../../assets/images/codes/taf.jpg'),
        alt: 'Код прогнозов TAF',
        link: 'taf',
    },
    {
        title: 'SIGMET',
        description: 'Далеко-далеко, за словесными горами в стране гласных и согласных живут рыбные тексты. Переписывается одна за заголовок города!',
        image: require('../../assets/images/codes/sigmet.jpg'),
        alt: 'Код штормового предупреждения SIGMET',
        link: 'sigmet',
    },
    {
        title: 'КМИ-85 ОО',
        description: 'Далеко-далеко, за словесными горами в стране гласных и согласных живут рыбные тексты. Переписывается одна за заголовок города!',
        image: require('../../assets/images/codes/85-OO.jpg'),
        alt: 'Орнитологический код КМИ-85-ОО',
        link: '/codes/KMI-85-OO',
    },
    // {
    //     title: 'КМИ-85 ФП',
    //     description: 'Далеко-далеко, за словесными горами в стране гласных и согласных живут рыбные тексты. Переписывается одна за заголовок города!',
    //     image: require('../../assets/images/home/slider/1.jpg'),
    //     alt: 'Код фактической погоды КМИ-85-ФП',
    //     link: 'KMI-85-OO',
    // },
]

const Codes = () => {
    const navigate = useNavigate();

    const handleClick = (itemData, title, link) => {
        navigate(`${link}`, { state: { itemData, title, link } });

    };

    return (
        <Container mt='20px' justify='space-around' width='100%'>
            <ViewTitle>Метеорологические коды</ViewTitle>
            {codesPreview?.map((item, index) => (
                <CodesItemContainer
                    key={index}
                    position='relative'
                    width='300px'
                    height='450px'
                    cursor='pointer'
                    onClick={() => handleClick(item.data, item.title, item.link)}
                >

                    <CodesTitle>{item.title}</CodesTitle>
                    <CodesDescription>{item.description}</CodesDescription>
                    <CodesImage src={item.image} alt={item.alt} />
                </CodesItemContainer>
            ))}
        </Container>
    );
};


export default Codes;

const CodesImage = styled.img`
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: brightness(75%);
    z-index: -1;
    &:hover {
    }
  `;

const CodesItemContainer = styled(Container)`
    overflow: hidden;
    padding: 10px;
    transition: 0.3s ease;
    margin-bottom: 20px;
    -webkit-border-top-right-radius: 40px;
    -webkit-border-bottom-left-radius: 20px;
    -moz-border-radius-topright: 40px;
    -moz-border-radius-bottom-left: 30px;
    border-top-right-radius: 40px;
    border-bottom-left-radius: 30px;
    &:hover {
      -webkit-border-top-right-radius: 20px;
      -moz-border-radius-topright: 20px;
      border-top-right-radius: 20px;
      -webkit-border-bottom-left-radius: 10px;
      border-bottom-left-radius: 10px;
  
      ${CodesImage} {
        filter: brightness(45%);
      }
    }
  `;

const CodesTitle = styled.h3`
    color: #fff;
    font-size: 26px;
    text-align: center;
    width: 100%;
    text-shadow: 2px 2px 8px #000000;
  `;

const CodesDescription = styled.span`
    color: #fff;
    font-size: 12px;
    text-align: center;
    width: 100%;
  `;