const Calculators = () => {
    const navigate = useNavigate();

    const handleClick = (link) => {
        navigate(`${link}`);
    };

    return (
        <CalculatorsContainer width='100%' padding='10px'>
            <ViewTitle>
                Калькуляторы для расчета метеорологических величин
            </ViewTitle>
            <CalcCategoryItem>
                {calculatorsItems?.map((calcItem, index) => (
                    <Collapse title={calcItem.title} key={index}>
                        <Container column m='10px' align='flex-start'>
                            {calcItem?.items?.map((item, index) => (
                                <Container key={index} cursor='pointer'>
                                    <CalcSubTitle onClick={() => handleClick(item.link)}>
                                        {index + 1}. {item.title}
                                    </CalcSubTitle>
                                </Container>
                            ))}
                        </Container>
                    </Collapse>
                ))}
            </CalcCategoryItem>
        </CalculatorsContainer>
    );
};

export default Calculators;

const CalcCategoryItem = styled(Container)``;

const CalculatorsContainer = styled(Container)``;

const CalcSubTitle = styled.span`
    font-size: ${adaptiveSize(1920, 18, 240, 12)};
    font-family: 'Roboto Thin';
    letter-spacing: 1.1px;
    position: relative;
    margin-bottom: 5px;
    transition: 0.1s ease;
    &::after{
        position: absolute;
        content: '';
        width: 100%;
        height: 1px;
        bottom: -1px;
        left: 0;
        opacity: 0.6;
        transition: 0.1s ease;

    }
    &:hover{
        &::after{
            background-color: #000;
        }
    }
`