import React from 'react';
import styled from 'styled-components';
import { Container } from '../atoms';
import { useNavigate } from 'react-router-dom';
import { adaptiveSize } from '../../functions/adaptiveSize';

const CloudAtlasItem = ({ item }) => {

    return (
        <CloudContainer column>
            <CloudImage src={item.backgroundImage} alt='' />
            <CloudTitle>
                {item.title}
            </CloudTitle>
            <CloudDescription>
                Высота: {item.height}
            </CloudDescription>
            <CloudDescription>
                {item.description}
            </CloudDescription>
        </CloudContainer>
    );
}

export default CloudAtlasItem;

const CloudImage = styled.img`
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: brightness(75%);
    z-index: -1;
`

const CloudContainer = styled(Container)`
    position: relative;
    width: 80%;
    height: 200px;
    z-index: 0;
    overflow: hidden;
    padding: 10px;
    transition: 0.3s ease;
    -webkit-border-top-right-radius: 40px;
    -webkit-border-bottom-left-radius: 20px;
    -moz-border-radius-topright: 40px;
    -moz-border-radius-bottom-left: 30px;
    border-top-right-radius: 40px;
    border-bottom-left-radius: 30px;
    margin: 30px;
    cursor: pointer;
    justify-content: center;
    align-items: center;
    padding: 10px;
    @media (max-width: 300px) {
        height: 250px;
  }
    &:hover {
        width: 80%;
        height: 250px;
      -webkit-border-top-right-radius: 20px;
      -moz-border-radius-topright: 20px;
      border-top-right-radius: 20px;
      -webkit-border-bottom-left-radius: 10px;
      border-bottom-left-radius: 10px;
  
      ${CloudImage} {
        filter: brightness(45%);
      }
    }
`



const CloudTitle = styled.h3`
    font-size: ${adaptiveSize(1920, 34, 240, 13)};
    letter-spacing: 1.1px;
    font-family: 'Roboto Thin';
    z-index: 1;
    color: white;
    margin: 20px;

`

const CloudDescription = styled.span`
    font-size: ${adaptiveSize(1920, 20, 240, 11)};
    letter-spacing: 1.4px;
    font-family: 'Roboto Thin';
    z-index: 1;
    color: white;


`