import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import Container from '../../../atoms/Container';
import { Input, InputContainer, ViewTitle } from '../../../atoms';


const FateevCalc = () => {
    const [t850, setT850] = useState(0);
    const [deltatd850, setDeltatd850] = useState(0);
    const [deltatd700, setDeltatd700] = useState(0);
    const [t500, setT500] = useState(0);
    const [deltatd500, setDeltatd500] = useState(0);
    const [deltatd600, setDeltatd600] = useState(0);

    let a
    let result
    const calcFateev = () => {
        a = Number(t850) - Number(t500) - (Number(deltatd850) + Number(deltatd700) + Number(deltatd600) + Number(deltatd500))
        a > 0 || a == 0 ? result = 'В прогнозе следует указывать грозу' : result = 'В прогнозе указывать грозу не следует'
        return `A = ${a}, ${result}`
    }
    let res = calcFateev()

    return (
        <Container column justify='flex-start' align='flex-start' m='20px'>
            <Container column justyfy='start' align='start'>

                <Container column justyfy='start' align='start'>
                    <ViewTitle>
                        Расчёт гроз по методу Фатеева
                    </ViewTitle>
                    <TheoryText>
                        Совершенствуя метод Вайтинга, Н.П. Фатеев предложил использовать для прогноза гроз параметр А, который полнее учитывает распределение влажности по высотам.
                        По Н.П. Фатееву
                        А = Т850 –Т500 – (D850+D700+D600+D500). Обозначения в формуле пояснений не требуют. Если по расчетам получается, что А больше 0, то в прогнозе следует указывать грозу.
                    </TheoryText>
                </Container>
                <Container>
                    <Container>
                        <InputContainer>
                            <Input
                                onChange={(e) => setT850(e.target.value)}
                                value={t850}
                                type='number'
                            />
                            <Container>
                                <p>
                                    - Температура на поверхности 850 ГПа
                                </p>
                            </Container>
                        </InputContainer>
                        <InputContainer>
                            <Input
                                onChangeText={(e) => setT500(e.target.value)}
                                value={t500}
                                type='number'

                            />
                            <Container>
                                <p>
                                    - Температура на поверхности 500 ГПа
                                </p>
                            </Container>
                        </InputContainer>
                        <InputContainer>
                            <Input
                                onChangeText={(e) => setDeltatd850(e.target.value)}
                                value={deltatd850}
                                type='number'

                            />
                            <Container>
                                <p>
                                    - Дефицит точки росы на поверхности 850 ГПа
                                </p>
                            </Container>
                        </InputContainer>
                        <InputContainer>
                            <Input
                                onChangeText={(e) => setDeltatd700(e.target.value)}
                                value={deltatd700}
                                type='number'

                            />
                            <Container>
                                <p>
                                    - Дефицит точки росы на поверхности 700 ГПа
                                </p>
                            </Container>
                        </InputContainer>
                        <InputContainer>
                            <Input
                                onChangeText={(e) => setDeltatd500(e.target.value)}
                                value={deltatd500}
                                type='number'

                            />
                            <Container>
                                <p>
                                    - Дефицит точки росы на поверхности 500 ГПа
                                </p>
                            </Container>
                        </InputContainer>
                        <InputContainer>
                            <Input
                                onChangeText={(e) => setDeltatd600(e.target.value)}
                                value={deltatd600}
                                type='number'

                            />
                            <Container>
                                <p>
                                    - Дефицит точки росы на поверхности 600 ГПа
                                </p>
                            </Container>
                        </InputContainer>
                    </Container>
                </Container>
            </Container>
            <h3 >Результат: {res}</h3>
        </Container>
    );
};

export default FateevCalc;

const TheoryText = styled.span`
    margin: 20px;
    font-size: 16px;
    font-family: 'Roboto Thin'
`