import React from 'react';
import styled from 'styled-components';
import Container from '../atoms/Container';

const ArticleItem = ({ item, onClick }) => {
    return (
        <Container width='100%' justify='center' onClick={onClick} >
            <Container bg='rgba(15, 34, 239, 0.3)' column align='flex-start' br='5px' padding='10px' width='80%' transition='0.2s ease-in-out' hoverWidth='90%' mt='10px' cursor='pointer' >
                <ArticleTitle>
                    {item.title}
                </ArticleTitle>
                <ArticleDescription>
                    {item.description}
                </ArticleDescription>
            </Container>
        </Container>
    );
}

export default ArticleItem;

const ArticleTitle = styled.h3`

`

const ArticleDescription = styled.span`

`
