import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
    cursor:  ${props => props.cursor ? props.cursor : 'auto'};
    padding: ${props => props.padding ? props.padding : 'auto'};
    border-radius: ${props => props.br ? props.br : '0'};
    max-width: 100%;
    display:  ${props => props.inline ? 'inline-flex' : 'flex'};
    flex-direction: ${props => props.column ? 'column' : 'row'};
    justify-content: ${props => props.justify ? props.justify : 'space-between'};
    align-items: ${props => props.align ? props.align : 'center'};
    flex-wrap: wrap;
    background-color: ${props => props.bg ? props.bg : 'transparent'};
    height: ${props => props.height ? props.height : 'auto'};
    width: ${props => props.width ? props.width : 'auto'};
    padding-left: ${props => props.pl ? props.pl : 'auto'};
    padding-right: ${props => props.pr ? props.pr : 'auto'};
    margin: ${props => props.m ? props.m : '0'};
    margin-top: ${props => props.mt ? props.mt : '0'};
    transition: ${props => props.transition ? props.transition : 'auto'};
    border: ${props => props.border ? props.border : 'none'};
    translate: 0.6s ease;
    position: ${props => props.position ? props.position : 'default'};
    flex-direction: ${props => props.position ? 'reverse' : 'default'};

    &:hover{
        
        width: ${props => props.hoverWidth ? props.hoverWidth : props.width ? props.width : 'auto'};
    }

`

export default Container;
