import React, { useState } from 'react';
import styled from 'styled-components';
import Container from '../../../../atoms/Container';
import { Button, Input, InputContainer, ViewTitle } from '../../../../atoms';

const PreasureCalc = () => {
    const [t, setT] = useState(0);
    const [mm, setMm] = useState(true);
    const [preasure, setPreasure] = useState(0);
    const [height, setHeight] = useState(0);
    const [aimHeight, setAimHeight] = useState(0);
    const [aimHeight2, setAimHeight2] = useState(0);
    const [aimHeight3, setAimHeight3] = useState(0);

    let q; // барическая ступень
    let p;
    let pSea; // давление на уровне моря
    let pAim; // давление
    let pAim2; // давление
    let pAim3; // давление

    const calcPreasure = () => {
        p = mm ? preasure * 0.750062 : preasure;
        q = (8000 / Number(preasure)) * (1 + (1 / 273 * t));
        pSea = (Number(preasure) + (Number(height) / Number(q)));
        pAim = (Number(pSea) - (Number(aimHeight) / Number(q)));
        pAim2 = (Number(pSea) - (Number(aimHeight2) / Number(q)));
        pAim3 = (Number(pSea) - (Number(aimHeight3) / Number(q)));

        return (
            <ResultContainer column >
                <p>
                    Давление в месте наблюдения = ({mm ? `${Number(preasure).toFixed(2)} мм.рт.ст.` : `${Number(preasure).toFixed(2)} гПа`}) ({mm ? `${(preasure / 0.750062).toFixed(2)} гПа` : `${(preasure * 0.750062).toFixed(2)} мм.рт.ст.`})
                </p>
                <p>
                    {isFinite(q) ? `Барическая ступень = ${(q).toFixed(2)} м/${mm ? 'мм.рт.ст.' : 'гПа'} (${(mm ? (q * 0.750062) : q / 0.750062).toFixed(2)} м/${mm ? 'гПа' : 'мм.рт.ст.'})` : `Введите корректные значения`}
                </p>
                <p>
                    Давление на уровне моря = {(pSea).toFixed(2)} {mm ? 'мм.рт.ст.' : 'гПа'} ({(mm ? (pSea / 0.750062) : pSea * 0.750062).toFixed(2)} {mm ? 'гПа' : 'мм.рт.ст.'})
                </p>
                <p>
                    Давление на высоте {aimHeight} м = {(pAim).toFixed(2)} {mm ? 'мм.рт.ст.' : 'гПа'} ({(mm ? (pAim / 0.750062) : (pAim * 0.750062)).toFixed(2)} {mm ? 'гПа' : 'мм.рт.ст.'})
                </p>
                <p>
                    Давление на высоте {aimHeight2} м = {(pAim2).toFixed(2)} {mm ? 'мм.рт.ст.' : 'гПа'} ({(mm ? (pAim2 / 0.750062) : (pAim2 * 0.750062)).toFixed(2)} {mm ? 'гПа' : 'мм.рт.ст.'})
                </p>
                <p>
                    Давление на высоте {aimHeight3} м = {(pAim3).toFixed(2)} {mm ? 'мм.рт.ст.' : 'гПа'} ({(mm ? (pAim3 / 0.750062) : (pAim3 * 0.750062)).toFixed(2)} {mm ? 'гПа' : 'мм.рт.ст.'})
                </p>
            </ResultContainer>
        );
    };

    return (
        <Container justify='flex-start' align='flex-start' m='20px'>
            <Container column justyfy='start' align='start'>
                <ViewTitle>
                    Расчет барической ступени, давления на уровне моря и давления на определённой высоте
                </ViewTitle>
                <TheoryText>
                    1 мм.рт.ст. = 0.750062 гПа
                </TheoryText>
                <TheoryText>
                    Барическая ступень = 8000/p * (1 + (1/273 * T))
                </TheoryText>
                <TheoryText>
                    (АВИАЦИОННАЯ МЕТЕОРОЛОГИЯ Методические указания  по выполнению лабораторных и контрольной работ, Ульяновск, 2009 год)
                </TheoryText>
            </Container>
            <Container column justify='flex-start' align='flex-start'>
                <Button onClick={() => setMm(!mm)}>
                    <p>{mm ? 'Переключиться на гПа' : 'Переключиться на мм.рт.ст.'}</p>
                </Button>
                <InputContainer>
                    <Input
                        onChange={(e) => setT(e.target.value)}
                        value={t}
                        type="number"
                    />
                    <Container justify='flex-start' align='flex-start'>
                        <p>- температура в месте наблюдения</p>
                    </Container>
                </InputContainer>
                <InputContainer>
                    <Input
                        onChange={(e) => setPreasure(e.target.value)}
                        value={preasure}
                        type="number"
                    />
                    <Container>
                        <p>- Давление в месте наблюдения ({mm ? 'мм.рт.ст.' : 'гПа'})</p>
                    </Container>
                </InputContainer>
                <InputContainer>
                    <Input
                        onChange={(e) => setHeight(e.target.value)}
                        value={height}
                        type="number"
                    />
                    <Container>
                        <p>- Высота места наблюдения</p>
                    </Container>
                </InputContainer>
                <InputContainer>
                    <Input
                        onChange={(e) => setAimHeight(e.target.value)}
                        value={aimHeight}
                        type="number"
                    />
                    <Container>
                        <p>- Высота, к которой нужно привести давление в первой точке</p>
                    </Container>
                </InputContainer>
                <InputContainer>
                    <Input
                        onChange={(e) => setAimHeight2(e.target.value)}
                        value={aimHeight2}
                        type="number"
                    />
                    <Container>
                        <p>- Высота, к которой нужно привести давление во второй точке</p>
                    </Container>
                </InputContainer>
                <InputContainer>
                    <Input
                        onChange={(e) => setAimHeight3(e.target.value)}
                        value={aimHeight3}
                        type="number"
                    />
                    <Container>
                        <p>- Высота, к которой нужно привести давление в третьей точке</p>
                    </Container>
                </InputContainer>
            </Container>
            <h2>Результат:</h2>
            {calcPreasure()}
        </Container>
    );
};

export default PreasureCalc;

const TheoryText = styled.span`
    margin: 20px;
    font-size: 16px;
    font-family: 'Roboto Thin';
`

const ResultContainer = styled(Container)`
    margin: 0;
    padding-left: 20px;
    justify-content: start;
    align-items: start;
`