import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import Container from '../../../atoms/Container';
import { Input, InputContainer, ViewTitle } from '../../../atoms';


const KpoCalc = () => {
    const [prev, setPrev] = useState(0);
    const [wind, setWind] = useState(0);
    const [kv, setKv] = useState(0);
    const [t, setT] = useState(0);
    const [td, setTd] = useState(0);

    useEffect(() => {
        if (wind == 0) {
            setKv(1.00)
        } else if (wind == 1) {
            setKv(1.02)
        } else if (wind == 2) {
            setKv(1.04)
        } else if (wind == 3) {
            setKv(1.07)
        } else if (wind == 4) {
            setKv(1.11)
        } else if (wind == 5) {
            setKv(1.16)
        } else if (wind == 6) {
            setKv(1.22)
        } else if (wind == 7) {
            setKv(1.28)
        } else if (wind == 8) {
            setKv(1.32)
        } else if (wind == 9) {
            setKv(1.36)
        } else if (wind == 10) {
            setKv(1.39)
        } else if (wind == 11) {
            setKv(1.41)
        } else if (wind == 12) {
            setKv(1.43)
        } else if (wind == 13) {
            setKv(1.45)
        } else if (wind == 14) {
            setKv(1.46)
        } else if (wind == 15) {
            setKv(1.47)
        } else if (wind == 16) {
            setKv(1.48)
        } else if (wind > 16) {
            setKv(1.48 + ((wind - 16) * 0.01))
        }
    }, [wind]);
    let kpo

    const calcKpo = () => {
        kpo = Number(prev) + Number(kv) * Number(t) * (Number(t) - Number(td))
        return `Kv=${kv.toFixed(2)},
        Коэффициент пожароопасности за сутки = ${(kpo - prev).toFixed(0)}
        Коэффициент пожароопасности = ${kpo.toFixed(0)}`
    }
    let res = wind >= 0 ? calcKpo() : 'Введите корректную скорость ветра'

    return (
        <Container column justify='flex-start' align='flex-start' m='20px'>
            <Container column justyfy='start' align='start'>
                {/* <CloudImage src={kpo3} />
                <CloudImage src={kpo1} />
                <CloudImage src={kpo2} /> */}
                <Container column justyfy='start' align='start'>
                    <ViewTitle>
                        Расчёт коэффициента пожароопасности
                    </ViewTitle>
                    <TheoryText>
                        Для вычисления комплексного показателя пожарной опасности необходимы следующие данные:
                        - температура воздуха, точка росы (в °С), скорость ветра (в м/с) на 13 часов по местному времени или в ближайший к нему срок синхронных метеорологических наблюдений (15 ч);
                        - количество выпавших осадков за предыдущие сутки (в мм).


                        Комплексный показатель текущего дня (КП) определяется как сумма произведений коэффициента, учитывающего скорость ветра (Кv), на температуру воздуха (Т и разность между значением температуры воздуха и температурой точки росы (t). Расчет КП начинается после последнего дождя и проводится за каждый день. (n). Данные за каждый день суммируются нарастающим итогом.
                        Коэффициент пожарной опасности Кv, учитывающий влияние скорости ветра на возникновение и распространение лесных пожаров (V - скорость ветра, м/с)
                    </TheoryText>
                </Container>
                <InputContainer>
                    <Input
                        onChange={e => setPrev(e.target.value)}
                        value={prev}
                        type="number"
                    />
                    <Container>
                        <p>
                            - Предыдущее значение КПО
                        </p>
                    </Container>
                </InputContainer>
                <InputContainer>
                    <Input
                        onChange={(e) => setWind(e.target.value)}
                        value={wind}
                        type="number"
                    />
                    <Container>
                        <p>
                            - Скорость ветра (м/с)
                        </p>
                    </Container>
                </InputContainer>
                <InputContainer>
                    <Input
                        onChange={(e) => setT(e.target.value)}
                        value={t}
                        type='number'
                    />
                    <Container>
                        <p>
                            - Температура (°С)
                        </p>
                    </Container>
                </InputContainer>
                <InputContainer>
                    <Input
                        onChange={(e) => setTd(e.target.value)}
                        value={td}
                        type='number'
                    />
                    <Container>
                        <p>
                            - Точка росы (°С)
                        </p>
                    </Container>
                </InputContainer>

            </Container>
            <h3 >Результат: {res}</h3>
        </Container>
    );



    // return (
    //     <Container justify='flex-start' align='flex-start' m='20px'>
    //         <Container column justyfy='start' align='start'>
    //             <h2>
    //                 Расчёт коэффициента пожароопасности
    //             </h2>
    //             <TheoryText>
    //                 Для вычисления комплексного показателя пожарной опасности необходимы следующие данные:
    //                 - температура воздуха, точка росы (в °С), скорость ветра (в м/с) на 13 часов по местному времени или в ближайший к нему срок синхронных метеорологических наблюдений (15 ч);
    //                 - количество выпавших осадков за предыдущие сутки (в мм).


    //                 Комплексный показатель текущего дня (КП) определяется как сумма произведений коэффициента, учитывающего скорость ветра (Кv), на температуру воздуха (Т и разность между значением температуры воздуха и температурой точки росы (t). Расчет КП начинается после последнего дождя и проводится за каждый день. (n). Данные за каждый день суммируются нарастающим итогом.
    //                 Коэффициент пожарной опасности Кv, учитывающий влияние скорости ветра на возникновение и распространение лесных пожаров (V - скорость ветра, м/с)
    //             </TheoryText>
    //         </Container>
    //         <Container column justify='flex-start' align='flex-start'>
    //             <Button onClick={() => setMm(!mm)}>
    //                 <p>{mm ? 'Переключиться на гПа' : 'Переключиться на мм.рт.ст.'}</p>
    //             </Button>
    //             <Container>
    //                 <Input
    //                     onChange={(e) => setT(e.target.value)}
    //                     value={t}
    //                     type="number"
    //                 />
    //                 <Container justify='flex-start' align='flex-start'>
    //                     <p>- температура в месте наблюдения</p>
    //                 </Container>
    //             </Container>
    //             <Container>
    //                 <Input
    //                     onChange={(e) => setPreasure(e.target.value)}
    //                     value={preasure}
    //                     type="number"
    //                 />
    //                 <Container>
    //                     <p>- Давление в месте наблюдения ({mm ? 'мм.рт.ст.' : 'гПа'})</p>
    //                 </Container>
    //             </Container>
    //             <Container>
    //                 <Input
    //                     onChange={(e) => setHeight(e.target.value)}
    //                     value={height}
    //                     type="number"
    //                 />
    //                 <Container>
    //                     <p>- Высота места наблюдения</p>
    //                 </Container>
    //             </Container>
    //             <Container>
    //                 <Input
    //                     onChange={(e) => setAimHeight(e.target.value)}
    //                     value={aimHeight}
    //                     type="number"
    //                 />
    //                 <Container>
    //                     <p>- Высота, к которой нужно привести давление в первой точке</p>
    //                 </Container>
    //             </Container>
    //             <Container>
    //                 <Input
    //                     onChange={(e) => setAimHeight2(e.target.value)}
    //                     value={aimHeight2}
    //                     type="number"
    //                 />
    //                 <Container>
    //                     <p>- Высота, к которой нужно привести давление во второй точке</p>
    //                 </Container>
    //             </Container>
    //             <Container>
    //                 <Input
    //                     onChange={(e) => setAimHeight3(e.target.value)}
    //                     value={aimHeight3}
    //                     type="number"
    //                 />
    //                 <Container>
    //                     <p>- Высота, к которой нужно привести давление в третьей точке</p>
    //                 </Container>
    //             </Container>
    //         </Container>
    //         <h2>Результат:</h2>
    //         {calcPreasure()}
    //     </Container>
    // );
};

export default KpoCalc;

const TheoryText = styled.span`
    margin: 20px;
    font-size: 16px;
    font-family: 'Roboto Thin'
`