import React from 'react';
import { Container, ViewTitle } from '../atoms'
import styled from 'styled-components';
import { cloudAtlasData } from '../../content/articlesData/articlesContent/cloudContentData/cloudAtlasData';
import { useNavigate } from 'react-router-dom';
import { adaptiveSize } from '../../functions/adaptiveSize';



const CloudAtlas = () => {
    const navigate = useNavigate();

    const handleClick = (itemData, title) => {
        navigate(`/articles/cloudAtlas/${title}`, { state: { itemData, title } });

    };
    return (
        <Container width='100%'>
            <ViewTitle>
                Атлас облаков
            </ViewTitle>
            <Container column width='100%'>
                {
                    cloudAtlasData.map((item, index) => (
                        <CloudAtlasContainer key={index} position='relative' column align='center' justify='center'
                            onClick={() => handleClick(item, item.title)}
                        >
                            <CloudAtlasImage alt={item.title} src={item.backgroundImage} />
                            <CloudAtlasTitle>
                                {item.title}
                            </CloudAtlasTitle>
                            <CloudAtlasDescription>
                                {item.description}
                            </CloudAtlasDescription>
                        </CloudAtlasContainer>
                    ))
                }
            </Container>
        </Container>
    );
}

export default CloudAtlas;

const CloudAtlasImage = styled.img`
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: brightness(75%);
    z-index: -1;
`

const CloudAtlasTitle = styled.h3`
    z-index: 1;
    color: white;
    font-size: ${adaptiveSize(1920, 34, 240, 18)};
    margin-bottom: 20px;
    font-family: 'Roboto Thin';
    letter-spacing: 1.1px;
`

const CloudAtlasDescription = styled.span`
    z-index: 1;
    color: white;
    font-family: 'Roboto Thin';
    font-size: ${adaptiveSize(1920, 24, 240, 14)};

`

const CloudAtlasContainer = styled(Container)`
    width: 80%;
    height: 200px;
    z-index: 0;
    overflow: hidden;
    padding: 10px;
    transition: 0.3s ease;
    -webkit-border-top-right-radius: 40px;
    -webkit-border-bottom-left-radius: 20px;
    -moz-border-radius-topright: 40px;
    -moz-border-radius-bottom-left: 30px;
    border-top-right-radius: 40px;
    border-bottom-left-radius: 30px;
    margin: 30px;
    cursor: pointer;
    
    &:hover {
        width: 80%;
        height: 250px;
      -webkit-border-top-right-radius: 20px;
      -moz-border-radius-topright: 20px;
      border-top-right-radius: 20px;
      -webkit-border-bottom-left-radius: 10px;
      border-bottom-left-radius: 10px;
  
      ${CloudAtlasImage} {
        filter: brightness(45%);
      }
    }
 
`