import { bp } from "../constants/breakpoints";

// Данная функция позволяет добавлять в styled-componens расчет значений исходя из размера экрана, 
// например: 'font-size: ${adaptiveSize(1200, 30, 320, 16)};', получаем значение в пикселях

const adaptiveSize = (largeScreen, largeSize, minScreen, minSize) => `
    calc(${minSize}px + ${largeSize - minSize} * ((100vw - ${minScreen}px) / ${largeScreen}))
`;

// С исходными значениями максимального и минимального экрана
const adaptiveSizeWithConst = (largeSize, minSize) => `
    calc(${bp.small}px + ${largeSize - minSize} * ((100vw - ${bp.small}px) / ${bp.big}))
`;

export {
    adaptiveSize,
    adaptiveSizeWithConst
}