import React from 'react';
import Container from '../atoms/Container';
import ArticleItem from '../organizmes/ArticleItem';
import { ViewTitle } from '../atoms';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';

const articles = [
    {
        title: 'Атлас облаков',
        description: 'Виды облаков',
        link: '/articles/cloudAtlas'
    },
    {
        title: 'Орнитологический атлас',
        description: 'Основные виды птиц',
        link: '/articles/ornitologyAtlas'
    },
    {
        title: 'Явления погоды',
        description: 'Описание основных явлений погоды',
        link: '/articles/weather'
    },
    {
        title: 'Синоптические объекты',
        description: 'Описание основных синоптических объектов',
        link: '/articles/synop'
    },
    {
        title: 'История метеорологической службы',
        description: '',
        link: '/articles/historyMil'
    },
    {
        title: 'Известные метеорологи',
        description: '',
        link: '/articles/great'
    },

]

const Articles = () => {
    const navigate = useNavigate();

    const handleClick = (link) => {
        navigate(`${link}`);
    };
    return (
        <ArticlesContainer width='100%' justify='center' padding='2%'>
            <ViewTitle>
                Статьи в части касающейся метеорологической службы
            </ViewTitle>
            {
                articles.map((item, index) => (
                    <ArticleItem onClick={() => handleClick(item.link)} item={item} key={index} />

                ))
            }
        </ArticlesContainer>
    );
}

export default Articles;

const ArticlesContainer = styled(Container)`
    padding: 2%;
`