import React from 'react';
import { Container } from '../atoms';
import styled from 'styled-components';
import { adaptiveSize } from '../../functions/adaptiveSize';

const NoMatch = () => {
    return (
        <Container width='100%' height='85vh'>
            <StyledText>Данная функция не реализована, но, возможно, появится в следующих версиях!</StyledText>
        </Container>
    );
}

export default NoMatch;

const StyledText = styled.h2`
    font-size: ${adaptiveSize(1920, 40, 240, 18)};
    font-family: 'Roboto Bold Italic';
    width: 100%;
    text-align: center;
    padding: 20px;
`