import React, { useState } from 'react';
import styled from 'styled-components';
import { adaptiveSize } from '../../functions/adaptiveSize';

const CollapseWrapper = styled.div`
    width: 100vw;
    margin-top: 10px;
    margin-bottom: 5px;
    margin-right: 20px;
    
`;

const CollapseHeader = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  background-color: rgba(15, 34, 239, 0.4);
  cursor: pointer;
  -webkit-border-top-right-radius: 15px;
-moz-border-radius-topright: 15px;
border-top-right-radius: 15px;
`;

const CollapseContent = styled.div`
  padding: 10px;
  background-color: #ffffff;
  transition: 0.2s ease-in-out;

`;

const Collapse = ({ title, children }) => {
    const [isCollapsed, setIsCollapsed] = useState(true);

    const toggleCollapse = () => {
        setIsCollapsed(!isCollapsed);
    };

    return (
        <CollapseWrapper>
            <CollapseHeader onClick={toggleCollapse}>
                <CollapseText>{title}</CollapseText>
                <CollapseText>{isCollapsed ? '+' : '-'}</CollapseText>
            </CollapseHeader>
            {!isCollapsed && <CollapseContent>{children}</CollapseContent>}
        </CollapseWrapper>
    );
};

export default Collapse;

const CollapseText = styled.span`
    font-family: 'Roboto Medium';
    font-size: ${adaptiveSize(1920, 20, 240, 14)};
`