import React, { useState } from 'react';
import styled from 'styled-components';
import { Container } from '../atoms';
import { adaptiveSize } from '../../functions/adaptiveSize';

const MobileMenuWrapper = styled.div`
  position: relative;
  display: none;
  width: auto;

  @media (max-width: 1500px) {
    display: flex;
    justify-content: end;
  }
`;

const MobileMenuButton = styled.button`
  background-color: transparent;
  border: none;
  padding: 10px;
  font-size: ${adaptiveSize(1920, 20, 240, 14)};
  color: #ffffff;
  cursor: pointer;
`;

const MobileMenuContent = styled(Container)`
  position: absolute;
  top: 100%;
  left: 0;
  background-color: rgba(39, 52, 94, 1);
  padding: 10px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  z-index: 9999;
  /* height: 100vh; */
  align-items: start;
  justify-content: start;
  width: 300px;
  padding-right: 10px;
`;

const MobileNavigation = ({ children }) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  return (
    <MobileMenuWrapper>
      <MobileMenuButton onClick={toggleMenu}>
        {isOpen ? 'Закрыть меню' : 'Открыть меню'}
      </MobileMenuButton>
      {isOpen && <MobileMenuContent column >{children}</MobileMenuContent>}
    </MobileMenuWrapper>
  );
};

export default MobileNavigation;
