import React from 'react';
import { Button } from '../atoms';
import Container from '../atoms/Container';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay } from 'swiper';
import 'swiper/swiper.min.css';
import styled from 'styled-components';
import { adaptiveSize } from '../../functions/adaptiveSize';



const slides = [
    {
        id: 0,
        image: require('../../assets/images/home/slider/0.jpg'),
        title: 'Хороший работник',
        text: 'должен заранее знать, насколько его зарплата зависит от его усилий и сколько он может заработать.',
    },
    {
        id: 1,
        image: require('../../assets/images/home/slider/5.jpg'),
        title: 'Мы были',
        text: 'первыми, когда шли назад. Мы стали задними, когда пошли вперед. Теперь все дело в скорости. ',
    },
    {
        id: 2,
        image: require('../../assets/images/home/slider/1.jpg'),
        title: 'Синоптики',
        text: 'в прогнозе погоды не ошибаются, они лишь путают время и место.',
    },
    {
        id: 3,
        image: require('../../assets/images/home/slider/2.jpg'),
        title: 'Точный прогноз',
        text: 'погоды на завтра вы узнаете послезавтра.',
    },
    {
        id: 4,
        image: require('../../assets/images/home/slider/3.jpeg'),
        title: 'Самый долгосрочный прогноз: ',
        text: '«Весна наступит, лето придет, осень не запоздает...',
    },
    {
        id: 5,
        image: require('../../assets/images/home/slider/4.jpg'),
        title: 'Ну, стали мы точно делать прогноз...',
        text: 'Стало вам от этого легче? ',

    },
];

const Home = () => {
    return (
        <Container width='100%'>
            <Container height='92vh' width='100%'>
                <Swiper
                    modules={[Autoplay]}
                    spaceBetween={0}
                    slidesPerView={1}
                    loop={true}
                    navigation
                    pagination={{ clickable: true }}
                    autoplay={{
                        delay: 8000,
                        disableOnInteraction: false,
                    }}
                >
                    {slides.map((slide) => (
                        <SwiperSlide key={slide.id} width='100%' height='100%'>
                            <Slide>
                                <Image src={slide.image} alt={`Slide ${slide.id}`} />
                                <Text>
                                    <SliderTitle>{slide.title}</SliderTitle>
                                    <SliderDescription>{slide.text}</SliderDescription>
                                </Text>
                                <Button bigText scale big background={'rgba(34, 48, 220, 0.3)'} color={'white'}>Изучай матчасть сегодня, завтра и вчера!!!</Button>
                            </Slide>
                        </SwiperSlide>
                    ))}
                </Swiper>
            </Container>
            <Container width='100%' justify='center' align='center' >
                <DisclamerText>
                    Данная версия веб-приложения является демонстрационной версией, то есть многие функции не реализованы, но обязательно появятся (в случае актуальности данного веб-приложения) в следующих версиях.
                </DisclamerText>
            </Container>
        </Container>
    );
}

export default Home;

const Slide = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  /* padding: 20px; */
  width: 100%;
  /* max-width: 1280px; */
  margin: 0 auto;
  height: 92vh;
  position: relative;
  @media (max-width: 880px) {
    flex-direction: column;
    justify-content: center;
    min-height: 600px;
  }
`;

const Image = styled.img`
  width: 100%;
  /* max-width: 400px; */
  height: 100%;
  background-size: cover;
  object-fit: cover;
  position: absolute;
  filter: brightness(50%);
  z-index: -1;                
`;

const Text = styled.div`
    width: 50%;
    max-width: 800px;
    color: #fff;
    border: 2px solid white;
    padding: 20px;
    padding-top: 60px;
    padding-bottom: 60px;
    -webkit-border-top-left-radius: 40px;
-webkit-border-top-right-radius: 20px;
-webkit-border-bottom-right-radius: 60px;
-moz-border-radius-topleft: 40px;
-moz-border-radius-topright: 20px;
-moz-border-radius-bottomright: 60px;
border-top-left-radius: 40px;
border-top-right-radius: 20px;
border-bottom-right-radius: 60px;
background-color: rgba(188, 177, 177, 0.3);
@media (max-width: 880px) {
    width: 90%;
    margin-bottom: 5%;
    min-height: 240px;

  }
`;

const SliderTitle = styled.h3`
    margin-bottom: 15px;
    font-size: ${adaptiveSize(1920, 26, 240, 18)};
    font-family: 'Roboto Bold Italic';
    font-weight: 900;
    letter-spacing: 1.2px;
`

const SliderDescription = styled.span`
    font-size: ${adaptiveSize(1920, 18, 240, 14)};

`

const DisclamerText = styled.span`
    font-family: 'Roboto Thin';
    font-size: ${adaptiveSize(1920, 30, 240, 20)};
    letter-spacing: 1.1px;
    width: 100%;
    height: 100%;
    margin: 10px;
    text-align: center;
    display: flex;
    align-items: center;
`
const BigText = styled.h3`
        font-size: ${adaptiveSize(1920, 35, 240, 20)};

`

const SmallText = styled.span`
        font-size: ${adaptiveSize(1920, 30, 240, 18)};
        font-family: 'Roboto Thin';
`

const StyledContainer = styled(Container)`
    /* flex-direction: column; */
    width: 100%;
  &:hover{
    width: 100%;
  }
`

const StyledImage = styled.img`
    width: 50%;
    height: 300px;
`
const ReversedContainer = styled(StyledContainer)`
    flex-direction: row-reverse;
`