import React from 'react';
import Container from '../atoms/Container';
import { Link, NavLink } from 'react-router-dom';
import styled from 'styled-components';
import { MobileNavigation } from '../molecules';
import { adaptiveSize } from '../../functions/adaptiveSize';

const Navigation = () => {


    return (
        <NavMainContainer bg='rgba(39, 52, 94, 1)' height='8vh' pl='2%' justify='space-between'>
            <StyledLogo>Lazy Meteo</StyledLogo>

            <NavContainer height='100%' pr='2%'>
                <StyledLink to="/">Главная</StyledLink>
                <StyledLink to="/articles">Статьи</StyledLink>
                <StyledLink to="/calculators">Калькуляторы</StyledLink>
                <StyledLink to="/codes">Метеорологические коды</StyledLink>


                {/* <StyledLink to="/video">Видео</StyledLink> */}

            </NavContainer>
            <MobileNavigation column>
                <StyledLink to="/">Главная</StyledLink>
                <StyledLink to="/articles">Статьи</StyledLink>
                <StyledLink to="/calculators">Калькуляторы</StyledLink>
                <StyledLink to="/codes">Метеорологические коды</StyledLink>


                {/* <StyledLink to="/video">Видео</StyledLink> */}

            </MobileNavigation>


        </NavMainContainer>

    );
}

const StyledLink = styled(NavLink)`
    text-decoration: none;
    transition: color 0.2s ease-in-out;
    height: 100%;
    padding-left: 5px;
    padding-right: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ffffff;
    @media (max-width: 1500px){
        font-size: ${adaptiveSize(1920, 14, 240, 9)};

    }
    &:hover {
        color: #F80000;
        text-shadow: 2px 2px 2px #0056b3;
        color: #F80000;
    }
    &.active {
        color: #F80000;
        text-shadow: 2px 2px 2px #0056b3;
        color: #F80000;
  }
  @media (max-width: 1500px) {
    margin: 10px;
}

`
const StyledLogo = styled.h2`
    color: white;
    font-family: 'Roboto Thin', sans-serif;
    font-size: ${adaptiveSize(1920, 36, 240, 20)};

`
const NavContainer = styled(Container)`
@media (max-width: 1500px) {
    display: none;
  }
`

const NavMainContainer = styled(Container)`
    min-height: 50px;
`

export default Navigation;
