import styled from 'styled-components';
import { createGlobalStyle } from 'styled-components';
import { COLORS } from '../constants/colors';
import {
    RobotoBlack,
    RobotoBlackItalic,
    RobotoBold,
    RobotoBoldItalic,
    RobotoItalic,
    RobotoLight,
    RobotoLightItalic,
    RobotoMedium,
    RobotoMediumItalic,
    RobotoRegular,
    RobotoThin,
    RobotoThinItalic
} from '../constants/fonts';


const GlobalTheme = createGlobalStyle`

    
@font-face {
    font-family: 'Roboto Black';
    src: url(${RobotoBlack}) format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Roboto Black Italic';
    src: url(${RobotoBlackItalic}) format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Roboto Bold';
    src: url(${RobotoBold}) format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Roboto Bold Italic';
    src: url(${RobotoBoldItalic}) format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Roboto Italic';
    src: url(${RobotoItalic}) format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Roboto Light';
    src: url(${RobotoLight}) format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Roboto Light Italic';
    src: url(${RobotoLightItalic}) format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Roboto Medium';
    src: url(${RobotoMedium}) format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Roboto Medium Italic';
    src: url(${RobotoMediumItalic}) format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Roboto Regular';
    src: url(${RobotoRegular}) format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Roboto Thin';
    src: url(${RobotoThin}) format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Roboto Thin Italic';
    src: url(${RobotoThinItalic}) format('truetype');
    font-weight: normal;
    font-style: normal;
}
* {
    font-family: 'Roboto Regular', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  a {
    text-decoration: none;
  }

  h1 {
    font-family: 'Roboto Black';
    letter-spacing: 1.1px;
  }

  h2 {
    font-family: 'Roboto Thin';
    font-weight: 100;
    letter-spacing: 1.2px;
    font-size: 32px;
  }

  h3, h4, h5 {
    font-family: 'Roboto Medium';
  }

  body {
    margin: 0;
    padding: 0;
    background-color: ${COLORS.aliceBlue};
    overflow-x: hidden;
  }
`;

export default GlobalTheme;