import React from 'react';
import styled from 'styled-components';
import { Container } from '../atoms';
import { adaptiveSize } from '../../functions/adaptiveSize';

const Bottom = () => {
    return (
        <BottomContainer justify='center' align='center'>
            <BottomText>
                Lazy Meteo - для ленивых метеорологов
            </BottomText>
        </BottomContainer>
    );
}

export default Bottom;

const BottomContainer = styled(Container)`
    width: 100%;
    background-color: rgba(39, 52, 94, 1);
    height: 42px;
    padding: 10px;
    @media (max-width: 800px) {
        height: auto;
  }
`

const BottomText = styled.span`
    font-size: ${adaptiveSize(1920, 22, 240, 10)};
    font-family: 'Roboto Thin';
    letter-spacing: 1.3px;
    color: white;
`