import React from 'react';
import { Container, ViewTitle } from '../atoms';
import { useLocation } from 'react-router-dom';
import CloudAtlasItem from '../organizmes/CloudAtlasItem';

const CloudAtlasLayout = () => {
    const location = useLocation();
    const item = location?.state?.itemData || [];
    return (
        <Container column width='100%'>
            <ViewTitle>
                {item.title}
            </ViewTitle>
            <Container column width='100%'>
                {
                    item.items?.map((cloudItem, index) => (
                        <CloudAtlasItem item={cloudItem} key={index} />
                    ))
                }
            </Container>
        </Container>
    );
}

export default CloudAtlasLayout;
